.input-wrapper{
    background-color: #fff;
    width: 100%;
    border-radius: 100px;
    height: 2.5rem;
    padding: 0 15px;
    box-shadow: 0px 0px 8px #ddd;
    display: flex;
    align-items: center;
}

input{
    background-color: transparent;
    border: none;
    height: 100%;
    width: 100%;
    font-size: 1.25rem;
    margin-left: 5px;

}
input:focus{
    outline: none;
}
#search-icon{
    color: royalblue;
}